body {
  margin-left: 2%;
  margin-right: 2%;
}

@media only screen and (min-width: 1800px) {
  body {
    margin-left: 10%;
    margin-right: 10%;
  }
}

.container {
  grid-template-areas: "logo"
                       "example"
                       "passes"
                       "input"
                       "output"
                       "footer";
  display: grid;
}

.main-logo {
  height: 7rem;
}

.logo {
  text-align: center;
  grid-area: logo;
  place-self: center;
}

@media only screen and (min-width: 1200px) {
  .container {
    grid-template: "logo input output" 180px
                   "example input output" 100px
                   "passes input output" 2fr
                   "footer footer footer" 100px
                   / 20% 40% 40%;
    column-gap: 1em;
    margin-left: 2em;
    margin-right: 2em;
    display: grid;
  }

  .main-logo {
    height: 7em;
  }

  .logo {
    text-align: left;
    align-self: auto;
  }
}

.example {
  grid-area: example;
  align-self: flex-start;
}

.passes {
  grid-area: passes;
}

.input-editor {
  grid-area: input;
  overflow: auto;
}

.output-editor {
  grid-area: output;
  overflow: auto;
}

.footer {
  grid-area: footer;
  justify-self: center;
  margin-top: 2em;
}

.compile {
  margin-top: 1em;
}

button.toggle {
  margin-top: .5em;
  margin-right: .5em;
}

hr {
  text-align: center;
  background: #aaa;
  margin-left: auto;
  margin-right: auto;
}

